  </div>
</template>

<script>
import Ad from "@/components/Ad";

export default {
  data() {
    return {
      detaliList: {},
      images: [],
      showShare: false,
      options: [
        [
          { name: "", icon: "wechat" },
          { name: "", icon: "weibo" },
          { name: "QQ", icon: "qq" },
        ],
        [
          { name: "", icon: "link" },
          { name: "", icon: "poster" },
          { name: "", icon: "qrcode" },
        ],
      ],
      show: false,
      sku: {
        tree: [
          {
            k: "", // skuKeyName
            k_s: "s1", // skuKeyStrsku  list key value  id
            v: [
              {
                id: "1", // skuValueId id
                name: "", // skuValueName
                imgUrl: "", // 
                previewImgUrl: "", // 
              },
              {
                id: "2",
                name: "",
                imgUrl: "",
                previewImgUrl: "",
              },
            ],
            largeImageMode: true, //  
          },
        ],
        //  sku M  sku S 
        list: [
          {
            id: 2259, // skuId
            s1: "1", //  k_s  s1  id
            s2: "1", //  k_s  s2  id
            price: 10000, // 
            stock_num: 110, //  sku 
          },
          {
            id: 2260, // skuId
            s1: "2", //  k_s  s1  id
            s2: "2", //  k_s  s2  id
            price: 20000, // 
            stock_num: 220, //  sku 
